import React, { useCallback, useState } from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';

import {
  PasswordInput, Button, Input,
} from '@ya.praktikum/react-developer-burger-ui-components';
import { validateEmail } from '../../utils/validation';
import { useDispatch, useSelector } from '../../hooks';

import commonStyles from '../common.module.css';
import styles from './login.module.css';
import { loginUser } from '../../store/actions/auth';

export function LoginPage() {
  const dispatch = useDispatch();

  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');

  const { loading, error } = useSelector((state) => state.auth);

  const isEmailValid = useCallback(() => !!validateEmail(email), [email]);
  const isFormValid = useCallback(() => isEmailValid() && password !== '', [isEmailValid, password]);

  const login = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(loginUser({ email, password }));
  };

  // @ts-ignore
  return (
    <div className={commonStyles.content}>
      <div className={styles.loginContainer}>
        <form
          className={classNames('form-fields_width_100', 'mb-20')}
          onSubmit={login}
          data-test="login-form"
        >
          <div className={classNames('text', 'text_type_main-medium', 'mb-6', 'text_align_center')}></div>
          <div className={classNames('mb-6')}>
            <Input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder=" e-mail"
              error={email !== '' && !isEmailValid()}
              errorText="  email"
              disabled={!!loading}
              success={isEmailValid()}
            />
          </div>
          <div className={classNames('mb-6')}>
            <PasswordInput
              onChange={(e) => setPassword(e.target.value)}
              // disabled={loading}
              value={password}
              name="password"
              // className="mb-6"
            />
          </div>
          <div className="text_align_center">
            {error && <p className="text text_type_main-default text_color_inactive text_color_error mb-6">{error}</p>}
            <Button type="primary" size="large" disabled={!isFormValid() || !!loading}>
              
            </Button>
          </div>
        </form>
        <p className="text text_type_main-default text_color_inactive text_align_center mb-4">
            ?
          <Link to="/register" className={classNames('ml-2 text text_type_main-default', commonStyles.link)}></Link>
        </p>
        <p className="text text_type_main-default text_color_inactive text_align_center">
           ?
          <Link to="/forgot-password" className={classNames('ml-2 text text_type_main-default', commonStyles.link)}> </Link>
        </p>
      </div>
    </div>
  );
}
